import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import toast from 'react-hot-toast';

// Validation schema
const RegisterSchema = Yup.object().shape({
  full_name: Yup.string().required('Full name is required'),
  contact_type: Yup.string().required('Contact type is required').oneOf(['email', 'phone']),
  email: Yup.string().when('contact_type', {
    is: 'email',
    then: (schema) => schema.email('Invalid email').required('Email is required'),
    otherwise: (schema) => schema.email('Invalid email').notRequired(),
  }),
  phone_number: Yup.string().when('contact_type', {
    is: 'phone',
    then: (schema) => schema.matches(/^\+?[1-9]\d{1,14}$/, 'Invalid phone number').required('Phone number is required'),
    otherwise: (schema) => schema.notRequired(),
  }),
  password: Yup.string()
    .min(8, 'Password must be at least 8 characters')
    .required('Password is required'),
  confirm_password: Yup.string()
    .oneOf([Yup.ref('password')], 'Passwords must match')
    .required('Confirm password is required'),
});

const Register: React.FC = () => {
  const { register, sendOtp } = useAuth();
  const navigate = useNavigate();
  const [isSubmitting, setIsSubmitting] = useState(false);
  
  // Handle form submission
  const handleSubmit = async (values: any) => {
    try {
      setIsSubmitting(true);
      
      // Prepare registration data
      const registrationData = {
        full_name: values.full_name,
        email: values.contact_type === 'email' ? values.email : undefined,
        phone_number: values.contact_type === 'phone' ? values.phone_number : undefined,
        password: values.password,
        confirm_password: values.confirm_password,
      };
      
      // Register user
      await register(registrationData);
      
      // Send OTP for verification
      const contact = values.contact_type === 'email' ? values.email : values.phone_number;
      await sendOtp(contact, values.contact_type);
      
      // Navigate to OTP verification page with contact info
      navigate('/otp-verification', { 
        state: { 
          contact,
          contactType: values.contact_type 
        } 
      });
    } catch (error) {
      console.error('Registration error:', error);
    } finally {
      setIsSubmitting(false);
    }
  };
  
  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-md w-full space-y-8">
        <div>
          <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
            Create your account
          </h2>
          <p className="mt-2 text-center text-sm text-gray-600">
            Or{' '}
            <Link to="/login" className="font-medium text-primary-600 hover:text-primary-500">
              sign in to your existing account
            </Link>
          </p>
        </div>
        
        <Formik
          initialValues={{
            full_name: '',
            contact_type: 'email',
            email: '',
            phone_number: '',
            password: '',
            confirm_password: '',
          }}
          validationSchema={RegisterSchema}
          onSubmit={handleSubmit}
        >
          {({ values, errors, touched, setFieldValue }) => (
            <Form className="mt-8 space-y-6">
              <div className="rounded-md shadow-sm -space-y-px">
                <div>
                  <label htmlFor="full_name" className="sr-only">
                    Full Name
                  </label>
                  <Field
                    id="full_name"
                    name="full_name"
                    type="text"
                    autoComplete="name"
                    className={`appearance-none rounded-none relative block w-full px-3 py-2 border ${
                      errors.full_name && touched.full_name
                        ? 'border-red-300 placeholder-red-500 focus:ring-red-500 focus:border-red-500'
                        : 'border-gray-300 placeholder-gray-500 focus:ring-primary-500 focus:border-primary-500'
                    } text-gray-900 rounded-t-md focus:outline-none focus:z-10 sm:text-sm`}
                    placeholder="Full name"
                  />
                  <ErrorMessage
                    name="full_name"
                    component="div"
                    className="text-red-500 text-xs mt-1"
                  />
                </div>
                
                <div className="py-4">
                  <div className="flex items-center space-x-4">
                    <label className="inline-flex items-center">
                      <input
                        type="radio"
                        name="contact_type"
                        value="email"
                        checked={values.contact_type === 'email'}
                        onChange={() => setFieldValue('contact_type', 'email')}
                        className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                      />
                      <span className="ml-2 text-sm text-gray-700">Email</span>
                    </label>
                    <label className="inline-flex items-center">
                      <input
                        type="radio"
                        name="contact_type"
                        value="phone"
                        checked={values.contact_type === 'phone'}
                        onChange={() => setFieldValue('contact_type', 'phone')}
                        className="h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                      />
                      <span className="ml-2 text-sm text-gray-700">Phone</span>
                    </label>
                  </div>
                </div>
                
                {values.contact_type === 'email' ? (
                  <div>
                    <label htmlFor="email" className="sr-only">
                      Email address
                    </label>
                    <Field
                      id="email"
                      name="email"
                      type="email"
                      autoComplete="email"
                      className={`appearance-none rounded-none relative block w-full px-3 py-2 border ${
                        errors.email && touched.email
                          ? 'border-red-300 placeholder-red-500 focus:ring-red-500 focus:border-red-500'
                          : 'border-gray-300 placeholder-gray-500 focus:ring-primary-500 focus:border-primary-500'
                      } text-gray-900 focus:outline-none focus:z-10 sm:text-sm`}
                      placeholder="Email address"
                    />
                    <ErrorMessage
                      name="email"
                      component="div"
                      className="text-red-500 text-xs mt-1"
                    />
                  </div>
                ) : (
                  <div>
                    <label htmlFor="phone_number" className="sr-only">
                      Phone Number
                    </label>
                    <Field
                      id="phone_number"
                      name="phone_number"
                      type="tel"
                      autoComplete="tel"
                      className={`appearance-none rounded-none relative block w-full px-3 py-2 border ${
                        errors.phone_number && touched.phone_number
                          ? 'border-red-300 placeholder-red-500 focus:ring-red-500 focus:border-red-500'
                          : 'border-gray-300 placeholder-gray-500 focus:ring-primary-500 focus:border-primary-500'
                      } text-gray-900 focus:outline-none focus:z-10 sm:text-sm`}
                      placeholder="Phone number (with country code)"
                    />
                    <ErrorMessage
                      name="phone_number"
                      component="div"
                      className="text-red-500 text-xs mt-1"
                    />
                  </div>
                )}
                
                <div>
                  <label htmlFor="password" className="sr-only">
                    Password
                  </label>
                  <Field
                    id="password"
                    name="password"
                    type="password"
                    autoComplete="new-password"
                    className={`appearance-none rounded-none relative block w-full px-3 py-2 border ${
                      errors.password && touched.password
                        ? 'border-red-300 placeholder-red-500 focus:ring-red-500 focus:border-red-500'
                        : 'border-gray-300 placeholder-gray-500 focus:ring-primary-500 focus:border-primary-500'
                      } text-gray-900 focus:outline-none focus:z-10 sm:text-sm`}
                    placeholder="Password"
                  />
                  <ErrorMessage
                    name="password"
                    component="div"
                    className="text-red-500 text-xs mt-1"
                  />
                </div>
                
                <div>
                  <label htmlFor="confirm_password" className="sr-only">
                    Confirm Password
                  </label>
                  <Field
                    id="confirm_password"
                    name="confirm_password"
                    type="password"
                    autoComplete="new-password"
                    className={`appearance-none rounded-none relative block w-full px-3 py-2 border ${
                      errors.confirm_password && touched.confirm_password
                        ? 'border-red-300 placeholder-red-500 focus:ring-red-500 focus:border-red-500'
                        : 'border-gray-300 placeholder-gray-500 focus:ring-primary-500 focus:border-primary-500'
                    } text-gray-900 rounded-b-md focus:outline-none focus:z-10 sm:text-sm`}
                    placeholder="Confirm password"
                  />
                  <ErrorMessage
                    name="confirm_password"
                    component="div"
                    className="text-red-500 text-xs mt-1"
                  />
                </div>
              </div>

              <div>
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-primary-600 hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 disabled:opacity-50"
                >
                  {isSubmitting ? 'Registering...' : 'Register'}
                </button>
              </div>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
};

export default Register; 