import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { useAuth } from './AuthContext';

// API base URL
const WS_URL = 'ws://localhost:8000/chat/ws';

// Types
interface Message {
  id: string;
  sender_id: string;
  receiver_id?: string;
  group_id?: string;
  content: string;
  message_type: string;
  file_url?: string;
  status: string;
  created_at: string;
}

interface TypingStatus {
  sender_id: string;
  receiver_id: string;
  is_typing: boolean;
}

interface ReadReceipt {
  message_id: string;
  reader_id: string;
}

interface WebSocketContextType {
  socket: WebSocket | null;
  isConnected: boolean;
  sendMessage: (content: string, receiverId?: string, groupId?: string, messageType?: string, fileUrl?: string) => void;
  sendTypingStatus: (receiverId: string, isTyping: boolean) => void;
  sendReadReceipt: (messageId: string) => void;
  messages: Message[];
  typingUsers: Record<string, boolean>;
}

interface WebSocketProviderProps {
  children: ReactNode;
}

// Create context
const WebSocketContext = createContext<WebSocketContextType | undefined>(undefined);

// WebSocket provider component
export const WebSocketProvider: React.FC<WebSocketProviderProps> = ({ children }) => {
  const { token, isAuthenticated } = useAuth();
  const [socket, setSocket] = useState<WebSocket | null>(null);
  const [isConnected, setIsConnected] = useState(false);
  const [messages, setMessages] = useState<Message[]>([]);
  const [typingUsers, setTypingUsers] = useState<Record<string, boolean>>({});

  // Connect to WebSocket when authenticated
  useEffect(() => {
    if (isAuthenticated && token) {
      const ws = new WebSocket(`${WS_URL}/${token}`);
      
      ws.onopen = () => {
        console.log('WebSocket connected');
        setIsConnected(true);
      };
      
      ws.onclose = () => {
        console.log('WebSocket disconnected');
        setIsConnected(false);
      };
      
      ws.onerror = (error) => {
        console.error('WebSocket error:', error);
      };
      
      ws.onmessage = (event) => {
        const data = JSON.parse(event.data);
        handleWebSocketMessage(data);
      };
      
      setSocket(ws);
      
      // Cleanup on unmount
      return () => {
        ws.close();
      };
    }
  }, [isAuthenticated, token]);
  
  // Handle incoming WebSocket messages
  const handleWebSocketMessage = (data: any) => {
    switch (data.type) {
      case 'message':
        setMessages((prevMessages) => [...prevMessages, data.data]);
        break;
      case 'typing':
        setTypingUsers((prevTypingUsers) => ({
          ...prevTypingUsers,
          [data.data.sender_id]: data.data.is_typing
        }));
        break;
      case 'read_receipt':
        setMessages((prevMessages) =>
          prevMessages.map((message) =>
            message.id === data.data.message_id
              ? { ...message, status: 'read' }
              : message
          )
        );
        break;
      default:
        console.log('Unknown message type:', data.type);
    }
  };
  
  // Send message function
  const sendMessage = (
    content: string,
    receiverId?: string,
    groupId?: string,
    messageType: string = 'text',
    fileUrl?: string
  ) => {
    if (socket && isConnected) {
      const messageData = {
        type: 'message',
        data: {
          content,
          receiver_id: receiverId,
          group_id: groupId,
          message_type: messageType,
          file_url: fileUrl
        }
      };
      
      socket.send(JSON.stringify(messageData));
    }
  };
  
  // Send typing status function
  const sendTypingStatus = (receiverId: string, isTyping: boolean) => {
    if (socket && isConnected) {
      const typingData = {
        type: 'typing',
        data: {
          receiver_id: receiverId,
          is_typing: isTyping
        }
      };
      
      socket.send(JSON.stringify(typingData));
    }
  };
  
  // Send read receipt function
  const sendReadReceipt = (messageId: string) => {
    if (socket && isConnected) {
      const readReceiptData = {
        type: 'read_receipt',
        data: {
          message_id: messageId
        }
      };
      
      socket.send(JSON.stringify(readReceiptData));
    }
  };
  
  // Context value
  const value = {
    socket,
    isConnected,
    sendMessage,
    sendTypingStatus,
    sendReadReceipt,
    messages,
    typingUsers
  };
  
  return (
    <WebSocketContext.Provider value={value}>
      {children}
    </WebSocketContext.Provider>
  );
};

// Custom hook to use the WebSocket context
export const useWebSocket = () => {
  const context = useContext(WebSocketContext);
  
  if (context === undefined) {
    throw new Error('useWebSocket must be used within a WebSocketProvider');
  }
  
  return context;
}; 